@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@700&family=Lobster+Two&family=Pacifico&family=Source+Sans+3:wght@500&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Source Sans 3', sans-serif;
  text-decoration: none;
}

a {
  color: white;
}

/* Helpers */
.container {
  width: 1200px;
  margin: 0 auto;
}

.h-650 {
  height: 650px;
}
.alignCenter {
  align-items: center;
}
.justifyConter {
  justify-content: center;
}

/* Grid System */
.row {
  display: flex;
  width: 100%;
  height: 50%;
  flex-wrap: wrap;
}
.col-4 {
  width: 30%;
}
.underline {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: orange;
          text-decoration-color: orange;
  text-decoration-thickness: 5px;
}

/* header */
/* Servives */
.services {

  background-image: linear-gradient(to right,gray,silver,#443844);
  padding: 100px 0;
}

.common {
  width: 33.33333333333333%;
  margin: 20px auto;
  text-align: center;
}

.heading {
  margin-bottom: 20px;
  font-weight: 500;
  color: #ff4a57;
  font-size: 16px;
}
.mainHeader {
  font-size: 32px;
  margin-bottom: 20px;
}

.mainContent {
  font-size: 16px;
}

.commonBorder {
  background:gray;
  height: 3px;
  width: 130px;
  margin: 30px auto;
}

.services__box {
  padding: 20px;
  margin: 20px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border:1px solid gray;
  border-radius: 5px;

}
.service__box-content{
  display:flex;
  flex-direction:row;

}

.commonIcons {
  font-size: 20px;
  color: #443844;
  margin-bottom: 15px;
}

.services__box-header {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 15px;
}

.services__box-p {
  font-size: 14px;
  padding-left:10px;
}

@media (max-width: 765px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }

  .col-6 {
    width: 100%;
  }
  .col-3 {
    width: 100%;
  }
  .col-4 {
    width: 100%;
  }


  .common {
    width: 100%;
  }
  .h-650 {
    height: auto;
  }
  .mainHeader {
    font-size: 20px;
  }
 


}



.bannertext__container {
  background-image: linear-gradient(to right, gray, #231123, black);
  height: 540px;
  width: 100%;
  padding-top: 30px;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

#banner__name {
  font-family: 'Cinzel Decorative', cursive;
  opacity: 0.6;
}

#banner__icon {
  font-size: 15rem;
  color: silver;
  opacity: 0.6;
  transition: 0.8s ease;
}

#banner__icon:hover {
  opacity: 1;
  transform: scale(1.2);
}

#admin__role {
  font-size: 14px;
  color: silver;
}


.banner__heading {
  padding-bottom: 7px;
}

.bannertext__container h1 {
  background-image: linear-gradient(60deg, gold, silver, gold);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  font-size: 55px;
  font-family: 'Cinzel', serif;
}

.right__block .admin {
  display: flex;
  flex-direction: row;
  padding-bottom: 35px;
  text-align: center;
}

.admin__info {
  display: grid;
  grid-template-rows: 20px 10px;
}


.admin__img {
  height: 50px;
  margin-right: 7px;
  transform: translateY(-4px);
  -webkit-clip-path: circle(50%);
  clip-path: circle(40%);
  max-width: 50px;
}

.two__btn {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.two__btn .show__more {
  color: silver;
  cursor: pointer;
  margin-bottom: 100px;

  background-image: linear-gradient(to left, rgb(54, 54, 190), #231123, #3aa3b6);
  padding: 12px 16px;
  font-weight: 600;
  text-decoration: none;
  border-radius: 5px;
  font-size: 13px;
  margin-right: 15px;
  font-weight: 600
}

.two__btn .show__more:hover {
  background-color: rgb(33, 29, 65)
}

.two__btn .explore__btn {
  color: black;
  cursor: pointer;
  margin-bottom: 100px;
  text-decoration: none;
  background-image: linear-gradient(to right, goldenrod, wheat);
  padding: 12px 16px;
  font-weight: 600;
  border-radius: 5px;
  font-size: 13px;
}

.two__btn .explore__btn:hover {
  background-color: grey;
}



.quote__box {
  max-width: 670px;
  margin-top: 10px;
  line-height: 26px;
}

.quote__box p {
  font-size: 1.2rem;
  opacity: 0.6;
  color: white;
}

.text {
  font-size: 1.6rem;
  opacity: 0.6;

}

.ChangingText:before {
  content: "";
  -webkit-animation: animate infinite 6s ease;
          animation: animate infinite 6s ease;
  font-weight: 600;
  color: white;
  font-family: 'Lobster Two', cursive;
  ;

}

@-webkit-keyframes animate {

  0% {
    content: "Full Stack Web Developer,";
  }

  70% {
    content: "UI / Theme Developer,";
  }

  100% {
    content: "Freelancer,";
  }
}

@keyframes animate {

  0% {
    content: "Full Stack Web Developer,";
  }

  70% {
    content: "UI / Theme Developer,";
  }

  100% {
    content: "Freelancer,";
  }
}

.btn {
  outline: none;
  border: none;
  text-decoration: none;
  margin: 15px;
}

.btn-outline {
  border-radius: 10px;
  padding: 10px 25px;
  font-size: 17px;
  font-weight: 420;
  transition: 0.5s;
  content: "";
  background: darkgray;
}

.btn-outline:hover {
  padding: 10px 40px;
}

.btn-smart {
  background: silver;
  padding: 13px 40px;
  border-radius: 50px;
}

@media (max-width: 455px) {


  .two__btn {
    transition: 0.6s ease;
    transform: translateY(-22px);
    margin: 5px;
  }

  .two__btn .show__more {
    width: 110px;
    font-size: 13px;
  }

  .two__btn .explore__btn {
    width: 140px;
    font-size: 13px;
  }

  #banner__icon #facode {
