    opacity: 0.6;
  }
}

@media (max-width: 825px) {


  .bannertext__container h1 {
    font-size: 30px;
    line-height: 35px;
    color: goldenrod;
    text-align: center;
  }

  #banner__name {
    font-size: 28px;
  }

  #banner__icon {
    opacity: 1;
    font-size: 7rem;
    pointer-events: none;
    transform: translateY(40px);
  }

  .bannertext__container {
    justify-content: space-around;
    background-image: linear-gradient(to right, gray, #231123, #7952B3);
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    align-items: center;

  }

  .left__block {
    padding-right: 0;
  }

  .quote__box {
    font-size: 10px;
  }

  .quote__box p {
    font-size: 16px;
    padding-right: 10px;
    padding-left: 20px;
    text-align: left;
    opacity: 0.6;
    color: antiquewhite
  }

  .two__btn {
    justify-content: center;
    align-items: center;
    transform: translateY(-35px);
  }

  #admin__role {
    color: silver;
    opacity: 0.5;
  }

  .headerIcon {
    font-size: 24px;
  }

  .banner__img {
    display: none;
  }

  .header__section p {
    font-size: 15px;
    font-weight: 400;
    line-height: 30px;
    padding-bottom: 50px;
  }

  .admin {
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 10px;
  }



  .bannertext__container .banner__logo {
    font-size: 27px;
  }

  .text {
    font-size: 1.4rem;
  }

}

