@media (max-width: 500px) {

  .Span {
    font-size: 22px;
    font-weight: 1000;
  }

}

@media (max-width: 565px) {
  .ln {
    display: none;
  }
}


@media (max-width: 765px) {
  .navbar__container {
    align-items: center;
    text-align: center;
    justify-content: center;
  }

  .navbar__title {
    margin: 0;
  }

  .ln {
    padding-right: 0px;
  }

  .nav-btn-outline {
    padding: 2px;
    margin: 10px;
    justify-content: center;
    color: silver;
  }

  .navbar__icons {
    display: none;
  }

  .upper_content {
    flex-direction: row;
    display: flex;
  }

  .navbar__right {
    display: flex;
    flex-direction: column;
    padding: 10px;
  }

  .ul__box {
    list-style-type: none;
    padding: 20px;
    text-align: left;
  }

  .ul__box li a {
    display: block;
    font-size: 16px;
    padding: 5px;
  }


  .toggle__box {
    visibility: visible;
    cursor: pointer;
  }

  .toggle__btn {
    color: silver;
    font-size: 20px;
  }
}

  img {
    object-fit: cover;
    width: 100%;
    transition:0.6s ease;
  }
  img:hover{
    transform:scale(1.1);
  }
 
  #f__container {
    background-image: linear-gradient(to right,gray,silver,#443844);
    height:100%;
    width: 100%;
    display:flex;
    align-items: center;
justify-content: center; 
/* Add empty space above the f__container (20% of the view height) */
    padding:130px 10px;
    justify-content: space-evenly;
  }
  
    
  .f__card {
    border: 1px solid darkgray;
    padding:1px;
    border-radius: 5px;
	max-width: 100%;
	margin: 30px;
	overflow: hidden;
	width: 300px;
	height:-webkit-max-content;
	height:-moz-max-content;
	height:max-content;
	transition:0.6s ease;
  }
  #Center{
      text-align: center;
  }
 
  
 
